"use client";

import { cn } from "@/shared/lib/utils";
import { VariantsSizes } from "./variants-sizes";
import { ProductColor, ProductSize, sizesProducts } from "@/shared/constants/products";
import { VariantsColors } from "./variants-colors";
import { ProductItem } from "@prisma/client";
import { Button } from "../ui";
import { useProductOptions } from "@/shared/hooks";


interface Props {
    imageUrl: string;
    alt: string;
    items: ProductItem[];
    onClickAddCart?: VoidFunction;
    className?: string;
}

export const ChoiceProduct: React.FC<Props> = ({  
    imageUrl, 
    alt,
    items,
    onClickAddCart,
    className 
}) => {     
    const {
        size,
        color,
        setSize,
        setColor,
        availableProducts,
        // availableSizes
    } = useProductOptions(items)

    const productPrice = items.find((item) => item.size === size && item.color === color)?.price
    const productOldPrice = items.find((item) => item.size === size && item.color === color)?.oldPrice
    const filteredSizes = sizesProducts.filter((item) => Number(item.value) === size)
    
    console.log({filteredSizes});

    const handleClickAddCart = () => {
        onClickAddCart?.();
        console.log({
            size,
            color
        });
    }

    return (
        <section className={cn(className,"product-item center")}>
            <div className="product-item__block-image">
                <img src={imageUrl} alt={alt} className="block-image__image" />
            </div>
            <div className="product-item__block-options">
                <div className="block-options__box-price">
                    {productPrice ? <p className="block-options__price">{productPrice}</p> : null}
                    {productOldPrice ? <p className="block-options__price_old">{productOldPrice}</p> : null}
                </div>
                <VariantsSizes 
                    items={items}
                    sizes={sizesProducts} 
                    onClick={value => setSize(Number(value) as ProductSize)}
                    selectedValue={String(size)}
                    size = {size}
                />
                <VariantsColors 
                    items={availableProducts}
                    onClick={value => setColor(Number(value) as ProductColor)}
                    selectedValue={String(color)}
                />
                
            </div>
        </section>
    );
};
